import React from "react";
// react plugin used to create charts
//import { Line } from "react-chartjs-2";
// reactstrap components

// core components
import MyNavbar from "components/Navbars/MyNavbar.js";
import MyHeader from "components/PageHeader/MyHeader.js";
import MyFooter from "components/Footer/MyFooter.js";

// sections for this page/view
import Proxy from "views/IndexSections/Proxy.js";


class ProxyPage extends React.Component {
  componentDidMount() {
    document.body.classList.toggle("proxy-page");
  }
  componentWillUnmount() {
    document.body.classList.toggle("proxy-page");
  }
  render() {
    return (
      <>
        <MyNavbar />
        <div className="wrapper">
          <MyHeader />
          <div className="main">
          <Proxy />
          </div>
          <MyFooter />
        </div>
      </>
    );
  }
}

export default ProxyPage;
